
import Rater from 'vux/src/components/rater/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'


export default {
name: 'DeviceMaintainList',
components: {
Rater,
Confirm
},
data () {
return {
error_msg: '',
confirm: false
}
},
props: {
maintain: Object
},
methods: {

onConfirm () {
event.stopPropagation()

if (this.maintain && this.maintain.id > 0 && this.maintain.status === 1) {
this.error_msg = '确认完成？'
this.confirm = true
}
},
onCancel () {
event.stopPropagation()
this.confirm = false
},
finishMaintain () {
event.stopPropagation()
this.confirm = false
let self = this
if (self.maintain && self.maintain.id > 0 && self.maintain.status === 1) {
let token = window.localStorage.getItem('token')
let url = self.api.finishPlan + '?id=' + self.maintain.id + '&token=' + token
self.utils.get(url, function (response) {
if (response.code === 200) {
let plan = response.data
self.maintain.status = plan.status
self.maintain.status_name = plan.status_name
}
}, self.$vux.confirm)
}
}
}
}



